* {
  box-sizing: border-box;
}
.suburst-container {
  text-align: center;
  padding: 4rem 0;
}
@media (max-width: 63.9375em) {
  .suburst-container {
    display: flex;
    padding: 0;
  }
}
@media (max-width: 39.9375em) {
  .suburst-container {
    width: 100%;
    padding: 0;
  }
}
.suburst-container #sunburst g .ancestor {
  fill-opacity: 0.15;
}
@media (min-width: 64em) {
  .text-column {
    padding-right: 4rem;
  }
}
.small-text {
  display: none;
}
@media (max-width: 39.9375em) {
  .small-text {
    display: block;
    padding-top: 2rem;
  }
}
@media (max-width: 39.9375em) {
  .large-text {
    display: none !important;
  }
}
h2,
h3 {
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: none;
  margin: 0;
}
h3 {
  font-size: 1.3rem;
}
h4 {
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: none;
  margin: 0 0 1rem 0;
  line-height: 1.4;
}
p {
  font-size: 1.1rem;
  line-height: 1.4;
  margin: 0;
}
h3 {
  margin-top: 1rem;
}
.sunburst-text {
  margin-top: 5rem;
}
@media (max-width: 39.9375em) {
  .sunburst-text {
    margin-top: 2rem;
  }
}
.sunburst-text .parent-data {
  font-weight: 700 !important;
  display: inline-block;
  margin-bottom: 1.5rem;
}
.sunburst-center-text {
  display: none;
}
@media (max-width: 39.9375em) {
  .sunburst-center-text {
    display: block;
  }
}
